import React, {useState} from 'react'
import { connect } from 'react-redux'
import authActions from '../redux/actions/authActions'
import Swal from 'sweetalert2'

const ForgotPassword = ( props ) => {
    const email = props.match.params.email
    const [usuario, setUsuario] = useState({ password:'', newPassword: '' })
    const [errores, setErrores] = useState ([])
    const [visible, setVisible] = useState(false)

    const readInput = e => { //receive the event
        const valor = e.target.value // capture the value
        const campo = e.target.name // capture the field

        setUsuario ({ //modify the user I have in the useState
            ...usuario,
            [campo]:valor
        });
    }

    const checkIfInputsAreEmpty = !usuario.password
    const changePassword = async e => { // function that runs when you click the create user button
        e.preventDefault() //prevent reloading the page
        if(checkIfInputsAreEmpty){
            alert ('Debe ingresar una nueva contraseÃ±a')
            return true
        }else if(usuario.password !== usuario.newPassword){
            alert('las contraseÃ±as no coinciden')
        }else{
            const respuesta = await props.newPassword(email, usuario.password)
            console.log(respuesta)
            if(respuesta && !respuesta.success){
                Swal.fire({
                    icon: 'error',
                    title: 'Ups!',
                    text: "Algo salio mal, intenta nuevamente!",
                    showConfirmButton: false,
                    timer: 4000
                    })
            }else {
                Swal.fire({
                    icon: 'success',
                    title: 'Genial!!',
                    text: "Tu contraseÃ±a se restablecio correctamente!",
                    showConfirmButton: false,
                    timer: 4000
                    })
            }
        }
    }

return (
    <div className="containerRegister">
        <div className="imagRegister"></div>
            <div className="registerInput" >
                <div className= "register">
                    <h2>Recuperar ContraseÃ±a</h2>
                    <div className="userNameAndPassword">
                        <div style={{display:'flex',alignItems:'center'}}>
                            <input className="inputRegister" type={visible ? "text" : "password"} name="password" placeholder="ContraseÃ±a" onChange={readInput} />
                            <input className="inputRegister" type={visible ? "text" : "password"} name="newPassword" placeholder="ContraseÃ±a" onChange={readInput} />
                            <i className={visible ? "far fa-eye-slash" : "far fa-eye"} onClick={()=>setVisible(!visible)}></i>
                        </div>
                    </div>
                    <button className="botonRegister" onClick={changePassword} >Restablecer ContraseÃ±a</button>
                </div>
            </div>
        <div style={{height:"50vh", width:"60vw"}}>
            {/* {errores.map(error => alert(error))} */}
        </div>
    </div>
        )
    }

const mapDispatchToProps = { // map the actions
    newPassword: authActions.newPassword //mapDispachToProps object that has an action value
}

export default connect(null,mapDispatchToProps) (ForgotPassword)